@import "_variables";

:root {
  font-size: 16px; // Base font size
  
  // Existing variables
  --bs-breadcrumb-divider: '';
  --bs-secondary-color: $primary;
}

body{
  font-family: $font-family-base;
  font-style: normal;
}

.animated-height {
  @include media-breakpoint-up(xl) {
    transform: scaleY(1);
    transform-origin: top;
    transition: all 2.3s ease-in-out;
  }

}

.p-16 {
  padding: 16px;
}

img {
  width: 100%;
  height: auto;
}

/* Custom media query for devices larger than 1440px */
.container {
  max-width: 100%;
  height: 100%;
}

main.container {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

@media (min-width: 1440px) {

  .container {
    max-width: 1440px;
    /* Custom width for screens 1440px and above */
  }
}

// OVerwrite Form-select styling

.form-select{
  line-height: 2.5rem;
  border-color: $primary;
}

.form-select:focus {
  border-color: $primary-green;
  box-shadow: 0 0 0 0.25rem rgba($primary-green, 0.25);
}



